body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #141E30;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 800px;
  height: 100vh;
  width: 80%;
  padding-bottom: 50px;
  box-sizing: border-box;
}

.color-block{
  width: 60%;
  height: 30%;
  border-radius: 15px;
  margin-bottom: 40px;
}

.btns{
  position: relative;
  width: 60%;
  display: flex;
  justify-content: space-around;
}

.btn{
  width: 30%;
  height: 40px;

  font-size: 16px;

  cursor: pointer;
  user-select: none;

  border-radius: 10px;
  border: 2px solid black;
  background-color: #e8ecef;

  transition: all .2s;
}

.btn:hover{
  background-color: #bababa;
}

.text{
  color: white;
  margin-bottom: 50px;
  font-size: 30px;
  line-height: 30px;

  color: red;
}


@media (max-width: 790px)
{
  .app{
    width: 90%;
    max-width: 90%;
  }

  .color-block{width: 90%}
  .btns{width: 90%; flex-direction: column; justify-content: center; align-items: center;}
  .btn{height: 60px; width: 70%; margin-bottom: 30px; font-size: 30px;}
}